import React, {useState} from 'react'
import { useStaticQuery, graphql,Link } from 'gatsby'
import GoogleMapReact from 'google-map-react'
import Pagination from '../../../../Global/Pages/Pagination.component'
import AdvanceSearchWidgetSidebar from '../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
import { HalfMapSearchResult } from '../../../../Global/Pages/Listing/HalfMap/HalfMapSearchResult.component'


const HalfMapV3Section=()=>{
    const [SideBarShow, setSideBarShow] = useState(false);
    const [SideBarMainShow, setSideBarMainShow] = useState(false);
    const handleMainSidebar=()=>{
        setSideBarMainShow(!SideBarMainShow)
        document.querySelector(".body_content").classList.toggle('translated')
    }
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
            document.querySelector('body').classList.toggle('body_overlay')
            document.querySelector('#mobilesidebar').classList.toggle('sidebar_ml0')
    }
    const data = useStaticQuery(graphql`
        query{
            fp1: file(relativePath: {eq: "property/fp1.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp2: file(relativePath: {eq: "property/fp2.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp3: file(relativePath: {eq: "property/fp3.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp4: file(relativePath: {eq: "property/fp4.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp5: file(relativePath: {eq: "property/fp5.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp15: file(relativePath: {eq: "property/fp15.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp16: file(relativePath: {eq: "property/fp16.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp17: file(relativePath: {eq: "property/fp17.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp18: file(relativePath: {eq: "property/fp18.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp19: file(relativePath: {eq: "property/fp19.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp20: file(relativePath: {eq: "property/fp20.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            author: file(relativePath: {eq: "property/pposter1.png"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
        }`)
    const ListPropertyItem=({listfor,featured, children,price ,category,title})=>{
    return(
        <div className="feat_property list">
            
            <div className="thumb">
                {children}
                <Link to="/single/v1">
                <div className="thmb_cntnt">
                    <ul className="icon mb0">
                        <li className="list-inline-item"><span><span className="flaticon-transfer-1"></span></span></li>
                        <li className="list-inline-item"><span><span className="flaticon-heart"></span></span></li>
                    </ul>
                </div>
                </Link>
            </div>
            <div className="details">
            <Link to="/single/v1">
                <div className="tc_content">
                    <div className="dtls_headr">
                        
                        {featured ?
                        <ul className="tag">
                                <li className="list-inline-item"><span>{listfor}</span></li>
                                <li className="list-inline-item"><span>Featured</span></li> 
                        </ul>
                        :
                            <ul className="tag">
                                <li className="list-inline-item"><span>{listfor}</span></li>
                                <li className="list-inline-item dn"><span></span></li> 
                            </ul>
                        }
                            
                        <span className="fp_price">${price}<small>/mo</small></span>
                    </div>
                    <p className="text-thm">{category}</p>
                    <h4>{title}</h4>
                    <p><span className="flaticon-placeholder"></span> 1421 San Pedro St, Los Angeles, CA 90015</p>
                    <ul className="prop_details mb0">
                        <li className="list-inline-item"><span>Beds: 4</span></li>
                        <li className="list-inline-item"><span>Baths: 2</span></li>
                        <li className="list-inline-item"><span>Sq Ft: 5280</span></li>
                    </ul>
                </div>
                </Link>
                <div className="fp_footer">
                    <ul className="fp_meta float-left mb0">
                        <li className="list-inline-item"><Link to="/agent/single"><img src={data.author.childImageSharp.fluid.originalImg} alt="pposter1.png"/></Link></li>
                        <li className="list-inline-item"><Link to="/agent/single">Ali Tufan</Link></li>
                    </ul>
                    <div className="fp_pdate float-right">4 years ago</div>
                </div>
            </div>
        </div>
)
    } 
    return(
    <div className="body_content">
        <section id="feature-property" className="our-listing bgc-f7 pt0 pb0">
		<div className="container-fluid">
            <div className="row">
                <div className="col-lg-12">
                        <div className="listing_sidebar dn db-991">
                            <div id="mobilesidebar"className="sidebar_content_details style3 ">
                                <div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
                                    <div className="sidebar_advanced_search_widget">
                                        <h4 className="mb25">
                                        Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                        </Link>
                                        </h4>
                                        <AdvanceSearchWidgetSidebar index="1" style2/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
            </div>
            <div className="row">
            <div className="col-sm-12">
                <div className="listing_sidebar dn-991">
                    <div id="mainsidebar" className="sidebar_content_details is-full-width style2">
                        <div className="sidebar_listing_list style2 mb0">
                            <div className="sidebar_advanced_search_widget">
                                <h4 className="mb25">Advanced Search</h4>
                                <AdvanceSearchWidgetSidebar index="2" style2/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-lg-12">
                    <div className="sidebar_switch mobile_style dn db-991 mt30 mt0-767 mb0">
                        <div id="main2">
                            <span id="open2" className="flaticon-filter-results-button filter_open_btn" onClick={handleSidebar}> Show Filter</span>
                        </div>
                    </div>
                </div>
				<div className="col-xl-6">
					<div className="half_map_area_content mt30">
						<div className="row">
							<HalfMapSearchResult more/>
						</div>
						<div className="row">
							<div className="col-lg-12">
                                <ListPropertyItem listfor='For Rent'
                                featured
                                price="13000"
                                category="Appartment"
                                title="Renovated Apartment"
                                >
                                    <img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                </ListPropertyItem>
                            </div>
                            <div className="col-lg-12">
                            <ListPropertyItem listfor='For Rent'
                                price="13000"category="Villa"
                                title="Gorgeous Villa Bay View"
                                >
                                    <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                </ListPropertyItem>
                            </div>
                            <div className="col-lg-12">
                                <ListPropertyItem listfor='For Sale'
                                    price="13000"category="Single Family Home"
                                    title="Luxury Family Home"
                                    >
                                        <img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp3.jpg"/>
                                </ListPropertyItem>
                            </div>
							
							<div className="col-lg-12 mb30">
								<Pagination/>
							</div>
                            </div>
						</div>
					</div>
                    <div className="col-xl-6">
                    <div className="sidebar_switch style text-right">
							<div id="main">
								{!SideBarMainShow ? 
                                    <span id="open3" className="showFilter filteropen flaticon-filter-results-button sidebarClosed" onClick={handleMainSidebar}>Show Filter</span>
                                        :
                                    <span id="open3" className="showFilter filteropen flaticon-close sidebarOpended" onClick={handleMainSidebar}>Hide Filter</span>
                                }
                            </div>
						</div>
					<div className="half_map_area">
						<div className="home_two_map style2">
							<div className="map-canvas skin2 half_style" id="contact-google-map" data-map-lat="51.510280" data-map-lng="-0.084028" data-icon-path="images/logo/1.png" data-map-title="Awesome Place" data-map-zoom="14">
                                <GoogleMapReact
						bootstrapURLKeys={{ key: 'AIzaSyAAz77U5XQuEME6TpftaMdX0bBelQxXRlM' }}
						defaultCenter={{
										lat: 51.510280,
										lng: -0.084028
										}}
						defaultZoom={14}
						>
							<div
								>
									Awesome Place
							</div>
        			</GoogleMapReact>
                            </div>
						</div>
					</div>
				</div>
                </div>
                </div>
	</section>
    </div>
    )

}
export default HalfMapV3Section